import React from 'react';
import styled from 'styled-components';
import {Link as link, useRouteMatch} from 'react-router-dom';

const Link = styled(link)`
  &&&{
    text-decoration: none;
    color: inherit;
  }
`;

const LinkWrapper = styled.div`
  padding: .5em 1em;
  font-weight: 500;
  color: ${({theme}) => theme.text.black};
  ${({theme}) => theme.emphasis.medium};
  transition: opacity 200ms ease-in-out;
  &:hover{
    ${({theme}) => theme.emphasis.high};
  }
  ${({active, theme}) => active && theme.emphasis.high};
`;

const NavLink = ({ to, label, exact}) => {
  let match = useRouteMatch({path: to, exact});
  return (
    <LinkWrapper active={match}>
      <Link to={to}>{label}</Link>
    </LinkWrapper>
  )
}
export default NavLink;