import React from 'react';
import styled from 'styled-components';
import {DiJsBadge as diJsBadge, DiHtml5 as diHtml5, DiCss3 as diCss3} from 'react-icons/di';

const Wrapper = styled.div`
  grid-area: footer;
  display: flex;
  align-items: center;
  grid-gap: .5em;
  padding: 1em;
  color: white;
  height: 3em;
`;

const DiJsBadge = styled(diJsBadge)`
  &&&{
    color: yellow;
    height: 2em;
    width: 2em;
  }
`;
const DiHtml5 = styled(diHtml5)`
  &&&{
    color: #FF4500;
    height: 2em;
    width: 2em;
  }
`;
const DiCss3 = styled(diCss3)`
  &&&{
    color: blue;
    height: 2em;
    width: 2em;
  }
`;

const Footer = () =>
  <Wrapper>
    <DiJsBadge />
    <DiHtml5 />
    <DiCss3 />
  </Wrapper>

export default Footer;