import {lighten, darken, transparentize} from 'polished'
// const color1 = '#17bebb'
// const color2 = '#2e282a'
// const color3 = '#cd5334'
// const color4 = '#edb88b'
// const color5 = '#fad8d6'

const color1 = '#1c77c3'
const color2 = '#39a9db'
const color3 = '#40bcd8'
const color4 = '#f39237'
const color5 = '#d63230'

const theme = {
  background: {
    // primary: '#121212',
    primary: `linear-gradient(30deg, ${darken(0.2,color1)}, ${lighten(0.2,color3)})`,
    secondary: '#fff',
    tertiary: '',
  },
  text: {
    white: '#fff',
    lightgray: '#c0c0c0',
    gray: '#808080',
    black: '#000000'
  },
  emphasis: {
    high: 'opacity: 0.87;',
    medium: 'opacity: 0.6;',
    low: 'opacity: .38;',
  },
  // Mateiral Design Shadow
  // https://github.com/material-components/material-components-web/blob/master/packages/mdc-elevation/_variables.scss
  
  materialShadow: (dp) => {
    const _color = 'black';
    let _shadow;
    let _opacity;
    // // umbra shadow
    // _opacity = 0.2;
    // switch (dp){
    //   case 2:
    //     _shadow = '0px 3px 1px -2px';
    //     break;
    //   case 4:
    //     _shadow = '0px 2px 4px -1px';
    //     break;
    //   case 8:
    //     _shadow = '0px 5px 5px -3px';
    //     break;
    //   case 16:
    //     _shadow = '0px 8px 10px -5px';
    //     break;
    //   default: _shadow = '0px 0px 0px 0px';
    // }
    
    // // penumbra shadow
    // _opacity = 0.14;
    // switch (dp){
    //   case 2:
    //     _shadow = '0px 2px 2px 0px';
    //     break;
    //   case 4:
    //     _shadow = '0px 4px 5px 0px';
    //     break;
    //   case 8:
    //     _shadow = '0px 8px 10px 1px';
    //     break;
    //   case 16:
    //     _shadow ='0px 16px 24px 2px';
    //     break;
    //   default: _shadow = '0px 0px 0px 0px';
    // }

    // ambient shadow
    _opacity = 0.12;
    switch (dp){
      case 2:
        _shadow = '0px 1px 5px 0px';
        break;
      case 4:
        _shadow = '0px 1px 10px 0px';
        break;
      case 8:
        _shadow = '0px 3px 14px 2px';
        break;
      case 16:
        _shadow = '0px 6px 30px 5px';
        break;
      default:  _shadow = '0px 0px 0px 0px';
    }

    return `
      box-shadow: ${_shadow} ${_color};
      opacity: ${_opacity};  
    `
  },
}
export default theme;